body {
  font-family: "Raleway-regular", sans-serif;
}

@media screen and (max-width: 1200px) {
  header .container nav .nav__menu {
    gap: 20px;
  }

  header .container nav .nav__menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  header .container nav .nav__menu .menu {
    display: flex;
    flex-wrap: wrap;
  }

  header .container nav .nav__menu .burger {
    display: none;
  }

  header .container {
    min-height: unset;
  }

  header .container .header__content {
    padding: 64px 10px 64px 10px;
    flex-wrap: wrap;
  }

  header .container .header__content p {
    margin: 32px 0px;
  }
}